import computed from './computed';
import { defaultProps, defaultOptions } from '../common/component-handler/press-component';
import PressIcon from '../press-icon/press-icon.vue';

const EXTRA_TYPE_MAP = {
  E_SPORT: 'e-sport',
  HOR: 'hor',
};

export default {
  name: 'PressEmpty',
  components: {
    PressIcon,
  },
  options: {
    ...defaultOptions,
    styleIsolation: 'shared',
  },
  props: {
    description: { type: String, default: '' },
    type: { type: String, default: '' },
    image: {
      type: String,
      default: 'default',
    },
    imageCustomStyle: {
      type: String,
      default: '',
    },
    customStyle: {
      type: String,
      default: '',
    },
    ...defaultProps,
  },
  emits: [],
  data() {
    return {
      computed,
    };
  },
  computed: {
    isESportType() {
      return this.type === EXTRA_TYPE_MAP.E_SPORT;
    },
    isHor() {
      return this.type === EXTRA_TYPE_MAP.HOR;
    },
    emptyClass() {
      const { isESportType, customClass, isHor } = this;

      return [
        isESportType ? 'press-empty--e-sport' : '',
        isHor ? 'press-empty--hor' : '',
        customClass,
      ].join(' ');
    },
  },
  mounted() {
  },
  methods: {
  },
};

